#truck{
height: 90px;
    width: 100px;
    color: rgb(188, 18, 18);
    background-color: white;
    font-size: 25px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
}
#icon{
height: 50px;
    width: 50px;
color: rgb(188, 18, 18);
    background-color: white;
    font-size: 20px;
    border-radius: 50%;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-top: -8px;
}
#card{
    border: none; background-color:#F0F5FD;display: flex;
        justify-content: center;
        align-items: center;
}
p{
    font-size: 15px;
    font-weight: 600;
    text-align: center;
    color:rgb(61, 59, 59);
}





.card:hover {
    transform: scale(1.05);
}
.card-wrapper {
    position: relative;
    overflow: hidden;
    transition: all 0.3s ease-in-out;

    &:hover {
        .image {
            filter: blur(1.4px);
            transform: scale(1.5);
            overflow: hidden;
            transition: all 0.3s linear;
           
        }

        .card-bottom {
            transform: translate(0%, -50%);
            transition: all 0.8s ease;
            background-color: rgba(54, 55, 53, 0.7);
        }
    }

    
}

.card-top {
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 1;
}

.card-bottom {
    width: 100%;
    position: absolute;
    z-index: 20;
    display: nonee;
    top: 50%;
    background-color: rgba(110, 122, 92, 0);
    padding: 100px 20px;
    color: #ffffff;
    transform: translate(100%, -50%);
}

.top-text {
    font-size: 25px;
    line-height: 40px;
    font-weight: bold;
    letter-spacing: 1px
}

.bottom-text {
    font-size: 15px;

}


.button {
    position: relative;
    display: block;
    outline: none;
    cursor: pointer;
    margin-top: 25px;
    text-align: center;
    border: none;
    border-radius: 3px;
    color: #ffffff;
    padding: 5px 20px
}
