* {
  margin: 0;
  padding: 0;
}
.Main {
  padding-top: 20vh;
  margin: 0;
}
#contact-form {
  width: 100%;
  height: 100%;
  max-width: 700px;
}
.form-control {
  padding: 10px;
  border-radius: 15px;
  font-size: 14px;
  color: gray;
  font-weight: 700;
}
.contact-wrapper {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  margin: 0 auto;
  padding: 20px;
  position: relative;
  max-width: 840px;
}
.send-button {
  margin-top: 15px;
  height: 42px;
  width: 600px;
  overflow: hidden;
  text-align: center;
  transition: all 0.2s ease-in-out;
  background-color: rgb(188, 18, 18);
  color: white;
}

.alt-send-button {
  width: 400px;
  height: 34px;
  transition: all 0.2s ease-in-out;
}

.send-text {
  display: block;
  margin-top: 10px;
  font: 700 12px "Lato", sans-serif;
  text-align: center;
  letter-spacing: 2px;
}
.send-button:hover {
  background-color: rgb(188, 18, 18);
  color: white;
}
.alt-send-button:hover {
  transform: translate3d(0px, -29px, 0px);
}
.search-container {
  position: relative;
  width: 230px;
}

.search-container input {
  width: 100%;
  padding: 8px 30px 5px 20px;
  border: 1px solid #ccc;
  border-radius: 4px;
  font-size: 16px;
  flex: 1;
  outline: none;
}
#iconSingle {
  height: 50px;
  width: 50px;
  color: rgb(188, 18, 18);
  background-color: rgba(238, 230, 230, 0.639);
  font-size: 20px;
  border-radius: 50%;
  display: flex;
  justify-content: center;
  align-items: center;
  margin-top: -8px;
}
#home {
  position: relative;
  width: 100%;
  height: 100vh;
  /* Full viewport height */
}

#home img {
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  object-fit: cover;
  z-index: -1;
  margin-top: -60px;
  /* Send the image to the back */
}
#home::after {
  content: "";
  position: absolute;
  top: 0;
  left: 0;
  width: 100%;
  height: 100%;
  background-color: rgba(0, 0, 0, 0.5);
  /* Black overlay with 50% opacity */
  z-index: 0;
  /* Place below text */
  margin-top: -60px;
}
#home h1,
#home p {
  position: absolute;
  /* Use absolute positioning for text */
  left: 48%;
  /* Center horizontally */
  transform: translateX(-50%);
  /* Adjust to truly center */
  color: white;
  /* Text color */
  text-align: center;
  /* Center the text */
  z-index: 1;
  /* Bring text above the image */
  margin: 0;
  /* Remove default margin */
}

#home h1 {
  font-size: 4.9em;
  font-family: chopin Script;
  margin-top: 10px;
  line-height: 86px;
  color: white;
  top: 200px;
}
#home p {
  font-size: 1em;
  color: white;
  top: 400px;
}
#about {
    position: relative;
    width: 100%;
    height: 50vh;

}

#about img {
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    z-index: -1;
    margin-top: -60px;
}

#about::after {
    content: "";
    position: absolute;
    top: 0;
    left: 0;
    width: 100%;
    height: 100%;
    background-color: rgba(0, 0, 0, 0.5);
    z-index: 0;
    margin-top: -60px;
}

#about h1,
#about p {
    position: absolute;
    left: 48%;
    transform: translateX(-50%);
    color: white;
    text-align: center;
    z-index: 1;
    margin: 0;
}

#about h1 {
    font-size: 3.0em;
    margin-top: 10px;
    line-height: 86px;
    color: rgb(200, 197, 197);
    top: 100px;
}

